import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  // mode: 'history',
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    { path: '*', component: () => import('../views/NotFound.vue'),  meta: {
        hideNavbar: true,
      }},
    {
      path: '/',
      name: 'notFound',
      component: () => import('../views/NotFound.vue'),
      meta: {
        hideNavbar: true,
      }
    },

    // Version 2 Voucher
    {
      // re=fe836868-8052-41a0-af22-495b3728f52e
      path: '/re=fe836868-8052-41a0-af22-495b3728f52e',
      name: 'redirectV2',
      component: () => import('../views/RedirectVoucher.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/promo-berakhir',
      name: 'promo-berakhir',
      component: () => import('../views/PromoBerakhir.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/form',
      name: 'form',
      component: () => import('../views/Form.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/voucher',
      name: 'voucher',
      component: () => import('../views/VoucherSuccess.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/voucher-habis',
      name: 'voucher-habis',
      component: () => import('../views/VoucherHabis.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/email-pernah-digunakan',
      name: 'email-pernah-digunakan',
      component: () => import('../views/EmailPernahDigunakan.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/ip-pernah-digunakan',
      name: 'ip-pernah-digunakan',
      component: () => import('../views/IpPernahDigunakan.vue'),
      meta: {
        hideNavbar: true,
      }
    },

    // Version 3 Product
      {
      // re=io85372-v92jtb-35f22-v0111-ob92uh4u
      path: '/re=io85372-v92jtb-35f22-v0111-ob92uh4u',
      name: 'redirectV3',
      component: () => import('../views/RedirectProduct.vue'),
      meta: {
        hideNavbar: true,
      }
    },
    {
      path: '/form3',
      name: 'form3',
      component: () => import('../views/Form3.vue'),
      meta: {
        hideNavbar: true,
      }
    },
     {
      path: '/item-habis',
      name: 'item-habis',
      component: () => import('../views/KuotaHabis.vue'),
      meta: {
        hideNavbar: true,
      }
    },
      {
      path: '/product',
      name: 'product',
      component: () => import('../views/ProductSuccess.vue'),
      meta: {
        hideNavbar: true,
      }
    },
  ]
})


export default router;